.App {
  text-align: center;
}

body{
  font-family: 'nanumsquare',arial,sans-serif;
  box-sizing: border-box;
  font-size: 16px;
}

hr{
  border-color: #D96299;
}

a{cursor: pointer;}

.notice{display: block; margin: 10px auto; font-size:0.9em;}

.logo{
  display: block;
  font-size: 0;
  width: 100px;
  box-sizing: border-box;
}
.logo img{
  width: 100%;
}

.d-flex{
  position: absolute;
  right:5%;
}

.topBtn{
  position: fixed;
  bottom: 5%;
  right: 3%;
  width: 50px;
  height: 50px;
  background: url(../public/img/expand_less.svg) no-repeat,linear-gradient(to bottom, #fc2ea2, #6a11a7);
  background-size: contain;
  font-size: 0;
  border: 0;
  border-radius: 50%;
  z-index: -1;
  opacity: 0;
  transition: all ease 1s;
}
.topBtn.active{opacity: 0.8; z-index: 99;}
.topBtn.active:hover{transform: translateY(-20%);}
.toBottom{position:relative; width: 5%; margin: 0 auto; animation: 2s ease infinite mousewheel;}
.toBottom img{width: 100%; margin-top: -65%;}

@-webkit-keyframes mousewheel {
  0% {
    top:85%;
    opacity: 0;
  }
  15%{
    top:85%;
    opacity: 1;
  }
  80%{
    top:90%;
    opacity: 1;
  }
  100% {
    top:90%;
    opacity: 0;
  }
}
@keyframes mousewheel {
  0% {
    top:85%;
    opacity: 0;
  }
  15%{
    top:85%;
    opacity: 1;
  }
  80%{
    top:90%;
    opacity: 1;
  }
  100% {
    top:92%;
    opacity: 0;
  }
}

.search_button{
  position: relative;
  display: block;
  font-size: 0;
  width: 45px;
  height: 45px;
  margin: 0; 
  border: none;
  background: url(../public/img/search_menu.svg)no-repeat;
  background-size: contain;
  transition: all ease 0.5s;
}

.search_button:hover,.search_button:active{
  background:url(../public/img/search_h.svg)no-repeat;
  background-size: contain;
  transition: all ease 0.5s;
}

.mainVisual{
  position: relative;
  width: 100%;
  height: 95vh;
  overflow: hidden;
  margin: 0;
  color: #fff;
}
.mainSlide{
  position: relative;
  width: 100%;
  height: 85vh;
  overflow: hidden;
  margin: 0;
  color: #fff;
}

.mainVisual_logo{
  width: 70%;
  height: 0;
  padding-bottom: 40%;
  margin: 0 auto;
  background: url(../public/img/logo1.png)no-repeat;
  background-size: contain;
  background-position: center center;
}

.mainVisual_text{
  position: absolute;
  top:50%;
  left: 50%;
  transform: translate(-50%,-50%);
  width: 80%;
}
.typing{
  position: absolute;
  width: 100%;
  top:30%;
  left: 50%;
  transform: translate(-50%,-50%);
  font-size: 2.4em;
  letter-spacing: -0.05em;
  padding: 0.1em 1em;
  text-shadow: 0 0 5px #000;
}

.container01{
  padding: 0 10%;
}

.container01>h2{
  padding-top:40px;
  padding-bottom: 10px;
}

.container01 h4{
  padding-top: 20px;
}

.album{
  width: 100%;
  box-shadow: 0px 10px 10px #b8b8b8;
  border-radius: 10px;
  transition: all ease 0.5s;
}
a:hover .album{
  transform: scale(1.1);
}

.album_col{
  position: relative;
  display: block;
  width: 100%;
  height: 10vw;
  min-height: 150px;
  overflow:hidden;
  border-radius: 10px;
  margin-bottom: 10px;
  text-decoration: none;
}

.album_col>span{
  display: block;
  position: relative;
  width: 100%;
  height: auto;
  padding: 0 10px;
  top:50%;
  left: 50%;
  transform: translate(-50%,-50%);
  z-index: 80;
}
.album_col h4{
  font-size: calc(12px + 1.5vw);;
  text-shadow: 0px 0px 5px #000;
  color: #fff;
  padding-top:0;
  margin-top: 0;
}
.album_col p{
  color: #aaa;
}

.album_col .album{
  position:absolute;
  top:-50%;
  left:0;
  filter:brightness(0.5) blur(3px);
  z-index: 10;
}

.album.card02{
  width: 80%;
  border-radius: 50%;
  margin-bottom: 20px;
}

.musicvideo{
  margin: 0 auto;
  width: 95%;
  height: 50vh;
  padding: 1em 0;
}

.conbox{
  width: 100%;
  margin: 0 auto;
  padding:100px 10% 100px 10%;
}

.circle{
  display: inline-block;
  width: 30%;
  min-width: 200px;
  height: 0;
  min-height: 200px;
  padding-bottom: 30%;
  background:linear-gradient(to bottom, #fc2ea2, #6a11a7);
  border-radius: 50%;
  vertical-align: middle;
}
.circle img{
  width: 100%;
}

.conbox p{
  display: inline-block;
  width: 70%;
  vertical-align: middle;
  padding-left: 20px;
  text-align: left;
  font-size: 1.15em;
}
.conbox p h2{
  display: block;
  font-size: 1.8em;
  padding: 10px 0;
}

.footer{
  display: block;
  position: relative;
  width: 100%;
  height:auto;
  background: #2f2645;
  padding: 20px 10%;
}

.footer_box{
  display: inline-block;
  position: relative;
  vertical-align: middle;
  color: #fff;
  overflow: hidden;
}
.footer_box>div{
  width: 50%;
  margin: 0 auto;
}
.footer_box>div img{
  display: block;
  margin: 0 auto;
  width:13vh;
}

.footer p{font-size: 12px;}

.carousel-inner{
  width: 100%;
  height: 85vh;
  vertical-align: middle;
}

.carousel-inner a{
  position: relative;
  display: inline-block;
  width:50vh;
  padding-left:10px;
  padding-right:10px;
}
.carousel-inner img{
  width: 90%;
  box-shadow: none;
}
.carousel-inner a:hover img{filter: brightness(0.4) blur(2px); transform: scale(1);}

.carousel-inner a p{display: inline-block; font-size: 1.5em;  background: rgba(0,0,0,0.8); padding: 10px 30px; margin-top:20px; border-radius: 40px;}
.carousel-inner a span{display: inline-block; font-size: 1.3em; vertical-align: middle;}


.carousel-inner a p:first-child{position: absolute; top: 36%; left: 50%; width: 80%; background: transparent; text-shadow: 0px 0px 5px #000; transform: translate(-50%,-64%); z-index: 80; opacity: 0; transition: all ease 0.5s;}
.carousel-inner a:hover p:first-child{opacity: 1;}

.carousel-caption{
  position:relative;
  top:0;
  left: 0;
  width: 100%;
  margin: 0;
  padding-top: 2%;
  padding-bottom: 2%;
  background: url(../public/img/slideback1.png) no-repeat;
  background-size: contain;
  background-position: center center;
}
.carousel-caption h4{font-size:3em; font-weight: bold; letter-spacing: -3px;}
.carousel-caption p{font-size:1.4em}

.slidefullbtn{
  width:80%;
  height:auto;
  background:url(../public/img/slideback2.png) no-repeat, linear-gradient(to bottom, transparent,#fff);
  background-size: auto 100%;
  background-position: right center;
  border-radius: 50px;
  border: none;
  padding: 2vw;
  text-align: left;

}
.slidefullbtn:hover{
  background:url(../public/img/slideback2.png) no-repeat, linear-gradient(to bottom, #fc2ea2,#fff);
  background-size: auto 100%;
  background-position: right center;
  transition: all ease 0.5s;
}

.slidefullbtn span{display: block; font-size: 1.8em; text-shadow: 0 0 5px #000;}
.slidefullbtn p{left: 0; display: inline-block; font-size: 2em; font-weight: bold; background: #fc2ea2; padding: 10px 40px; border-radius: 4vw; transition: all ease 0.5s;}
.slidefullbtn:hover p{background: #22173a;}
.slidefullbtn p:first-child{font-size: 1.2em;}
.slidefullbtn p:last-child{margin-top: 18%;}

.topsection{
  margin-bottom: 130px;
}
.section01,.section02{
  margin-bottom: 100px;
}

.topsection h2{
  display: inline-block;
  width: inherit;
  position:relative;
  padding: 80px 0;
  font-size:2.2em;
  margin: 0 auto;
  font-weight: 800;
}

.section01 h2{
  font-size:2.2em;
  padding-top:80px;
  padding-bottom: 25px;
}
.section02 h2{
  font-size:2.2em;
  padding-bottom: 25px;
}

.section03{background: #eee; padding: 5% 10%;}
.section03 h2{font-size:2em; padding-bottom: 25px;}
.section03 .line{width: 30%; margin: 20px auto;}

.topsection h2::before{
  content: "";
  display: block;
  position: absolute;
  top:75px;
  left:-15px;
  width: 25px;
  height: 25px;
  background:#fc2ea2;
  border-radius: 50%;
  z-index: -1;
}

.nav{
  width: inherit;
  margin: 0 auto;
  padding-bottom: 35px;
  font-size: 1.1em;
}

.nav-link{
  color: #6a11a7;
}

.nav-link.active{
  background:linear-gradient(to right, #fc2ea2, #6a11a7);
}

.nav-bar{position:absolute; width: 100%; z-index: 99; background:transparent; padding: 10px 5%;}

.container01 .btn{
  margin-top:35px;
  background:linear-gradient(to right, #fc2ea2, #6a11a7);
  border-radius: 10px;
  color: #fff;
  border: none;
}

.f_btn{
  border: none;
  background:transparent;
  color: #fc2ea2;
  font-weight: bold;
}

.modal h5{
  text-align: center;
  padding: 10px 0;
}

/*서치*/
.search_box{display:block; padding:10vh 10%; color: #fff;}
.search_box input {display: inline-block; width: 75%; background:linear-gradient(to right, #fc2ea2, #6a11a7); border: none; padding: 10px; color: #fff; text-align: center; border-radius: 30px;}
.search_box input::placeholder{color: #000;}
.search_box input:focus{outline: none;}
.search_box button{position: absolute; width: 30px; height: 30px; margin: 5px 10px; background: url(../public/img/search.svg); background-size: contain; font-size: 0; border: none;}
.white{color: #fff;}
.search_back{position: fixed; width: 100%; height: 100%; z-index: -1;}
.album.search{box-shadow: none; color: #fff; border-radius: 50%;}

/*디테일*/

.accordion-item{background: transparent !important;}

.vidtop-content{
  position: fixed;
  top: 0; right: 0; bottom: 0; left: 0;
  width: 100%;
  height: 100%;
  z-index: 90;
}
.vidtop-content .vidcon.left{
  position: absolute;
  width: auto;
  bottom: 0;
  left: 0;
}
.vidtop-content .vidcon.right{
  position: absolute;
  width: auto;
  bottom: 0;
  right: 0;
  text-align: right;
}

.video-foreground{
  position: fixed;
  z-index: 80;
}
.video-background {
  background: #000;
  position: fixed;
  top: 0; right: 0; bottom: 0; left: 0;
  z-index: 0;
}
.video-foreground,
.video-background iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.vidtop-content .card{
  margin: 0;
  padding: 0;
  background: transparent;
}

.vidtop-content .music_card{
  position:relative;
  top:10px;
  background: linear-gradient(to top, #fff ,transparent);
  border: none;
  border-radius: 0;
  text-align: center;
}

.vidtop-content .music_card .card-header{
  background: transparent;
  border: none;
  margin: 0;
  padding: 0;
}

.vidimg{
  width: 42.9vh;
  border-radius: 10px;
  overflow: hidden;
  margin: 0 auto;
}

.card h4{
  font-weight: bolder;
}

.detail_btn{
  width: 30px;
  height: 30px;
  margin: 0 10px;
  background: transparent;
  font-size: 0;
  border: none;
  overflow: hidden;
}
.detail_btn.mute.true{
  background-image: url(../public/img/volume_off.svg);
  background-size: contain;
}
.detail_btn.mute.false{
  background-image: url(../public/img/volume_up.svg);
  background-size: contain;
}

.detail_btn.pause.true{
  background-image: url(../public/img/pause.svg);
  background-size: contain;
}
.detail_btn.pause.false{
  background-image: url(../public/img/play_arrow.svg);
  background-size: contain;
}

.detail_btn.info.true{
  background-image: url(../public/img/expand_more.svg);
  background-size: contain;
}
.detail_btn.info.false{
  background-image: url(../public/img/expand_less.svg);
  background-size: contain;
}

.btn.link{
  background:linear-gradient(to right, #fc2ea2, #6a11a7);
  color: #fff;
  border: none;
  padding: 5px 20px;
  margin-bottom: 20px;
}

@media (min-aspect-ratio: 16/9) {
  .video-foreground { height: 300%; top: -100%; }
}
@media (max-aspect-ratio: 16/9) {
  .video-foreground { width: 300%; left: -100%; }
}
@media all and (max-width: 1455px) {
  body{
    font-size: 14px;
  }
  .toBottom{width: 6%;}
  .mainSlide{
    height: 55vw;
  }
  .carousel-inner{
    height: 55vw;
  }
  .carousel-inner a{
    position: relative;
    width:28vw;
  }
  .slidefullbtn span{font-size: 1.5em;}
  .slidefullbtn p:last-child{margin-top: 15%;}

}
@media all and (max-width: 1024px) {
  body{
    font-size: 14px;
  }
  .mainVisual{
    height: 80vh;
  }
  .mainVisual_logo{
    width: 100%;
    height: 50vh;
  }
  .typing{
    font-size: 2em;
  }
  .toBottom{width: 8%;}
  .mainSlide{
    height: 65vw;
  }
  .carousel-inner{
    height: 65vw;
  }
  .carousel-inner a{
    position: relative;
    width:28vw;
  }
  .slidefullbtn span{font-size: 1.2em;}
  .slidefullbtn p:last-child{margin-top: 15%;}
}
@media all and (max-width: 768px) {
  body{
    font-size: 13px;
  }
  .typing{
    width: 51%;
  }
  .mainSlide{
    height: 150vw;
  }
  .carousel-inner{
    height: 150vw;
  }
  .carousel-inner a{
    position: relative;
    width:45%;
    padding-top: 5vw;
  }
  .slidefullbtn span{font-size: 1.2em; padding: 5% 10%;}
  .slidefullbtn p:last-child{margin-top: 15%;}
  .carousel-inner a p{display: inline-block; font-size: 1.5em;  background: rgba(0,0,0,0.8); padding:1vw 3vw; margin-top:2vw; border-radius: 40px;}
  .carousel-inner hr{margin: 1vw 0;}

  .slidefullbtn{text-align: center; height: 110vw;}
  .slidefullbtn p{left: 0; display: inline-block; font-size: 2em; font-weight: bold; background: #fc2ea2; padding: 10px 40px; border-radius: 4vw; transition: all ease 0.5s;}
  .slidefullbtn:hover p{background: #22173a;}
  .slidefullbtn p:first-child{font-size: 1.2em;}
  .slidefullbtn p:last-child{margin-top:20px;}

  .carousel-caption h4{font-size:2.5em; padding-top: 2%;}
  .carousel-caption p{font-size:1.3em;}

  .topsection h2{font-size: 3em;}
  .section01 h2, .section02 h2{font-size:2.5em;}

  .section01 .col-md-3, .section00{
    height: 35vw;
  }

  .section01 .album, .section00 .album{
    position: relative;
    top:50%;
    left: 0;
    transform: translate(0,-50%);
    width: 40%;
    float: left;
  }
  .section01 a:hover .album, .section00 a:hover .album{
    transform: scale(1);
    transform: translate(0,-50%);
  }
  article{
    position: relative;
    top:50%;
    left: 0;
    transform: translate(0,-50%);
    display: inline-block;
    width: 50%;
    height: auto;
  }
  .section03 .col-md-3{
    width: 50%;
    float: left;
  }

  .vidtop-content .card{
    position: relative;
    width: 100%;
    height: auto;
    overflow: hidden;
  }
  .vidtop-content .vidimg{
    width: 100%;
  }

  .search_box{padding:15vh 10%; color: #fff; font-size: 1.2em;}
  .search_div .container01{
    padding: 0 20%;
  }

}
@media all and (max-width: 500px) {
  body{
    font-size: 12px;
  }
  .typing{
    width: 70%;
  }
  .mainSlide{
    height:0;
    padding-bottom: 180%;
  }
  .carousel-inner{
    height:0;
    padding-bottom: 180%;
  }
  .carousel-inner a{
    position: relative;
    width:40%;
    padding-top: 5vw;
  }
  .carousel-inner hr{
   margin-top:0;
   margin-bottom: 10px;
  }
  .carousel-caption h4{font-size:2em; padding-top: 5%;}
  .slidefullbtn{text-align: center; height: 130vw;}
  .slidefullbtn p:first-child{padding: 5px 20px; font-size: 1em;}
  .slidefullbtn span{font-size: 1.1em;}
  .slidefullbtn p:last-child{font-size: 1.3em; padding: 5px 20px;}

  .conbox{
    padding:100px 3% 100px 3%;
  }

  .section03{background: #eee; padding: 10% 10%;}
  .section03 h2{font-size:1.4em;}
  
}
@media all and (max-width: 375px) {
  body{
    position: relative;
    font-size: 12px;
  }
  .carousel-inner a{
    width:25vh;
  }
  .carousel-inner{
    height:0;
    padding-bottom: 180%;
  }
  .carousel-inner a{
    position: relative;
    width:40%;
    padding-top: 5vw;
  }
  
}